.header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
} 

.header-top h1 {
  margin: 0;
  left: 5px;
  top: 60px;
}

.code-symbol {
  font-size: 24px;
  color: white;
  font-family: 'PT Sans', sans-serif;
  font-style: italic;
  font-weight: 400;
  left: 1880px;
  position: absolute;
  top: 70px; 
}
h1 {
  margin-top: 20px;
  bottom: 10px;
  position: relative;
} 
.headerhouse {
  position: relative;
  overflow: visible;
  padding: 30px 0px;
  width: 1920px;
    height: 90px;
    display: flex;
    justify-content: center;
  background-color: rgb(32, 40, 70);
  z-index: 2;
  margin-top: 70px;
}

.header__row {
  width: 1603.55px;
  margin: 0 auto;
  padding: 10px 138px 20px;
  column-gap: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  width: 178.45px;
  height: 70px;
  padding-top: 10px;
  padding-left: 5px;
  padding-right: 10px;
  margin-bottom: 5px;
  margin-top: -35px;
  margin-left: -200px;
  margin-right: 10px;
  
}

.header__nav {
  Width: 1115px;
  Height: 56.28px;
  position: absolute;
  Top: 10px;
  Left: 500px;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
  letter-spacing: 0%;
  text-transform: uppercase;
  align-items: center;
  
}

.header__nav ul {
  display: flex;
  gap: 9px;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.header__nav a {
  color: var(--textColor);
  text-decoration: none;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: 25px 1px 3px 40px;
  border-left: 2px solid white;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 20px;
  display: flex;
  width: 180px;
  height: 54.31px;
  margin-top: 0px;
  
  
}


.globoImg {
  display: flex;
  width: 116px;
  height: 75px;
  justify-content: center;
  text-align: center;
  align-items: center;
  position: relative;
  padding: 10px 10px 25px 35px;
  margin-left: -700px;
  margin-right: -200px;
  margin-top: -25px;
  margin-bottom: -1px;
  
  }
  
.globoImg::before  {
  content: "";
  padding: 1px 20px 1px;
  transform: rotate(-90deg);
  margin-left: -250px;  
    margin-top: 35px;
    margin-bottom: 30px;
    width: 59px;
    height: 0px;
    background-color: white;
    position: absolute;
    border-left: 2px solid white;
  }
  
 
.globoImg span {
  color: var(--textColor);
  padding-left: -10px;
  Width: 60px;
  Height: 60px;
  Top: 10px;
  align-items: end;
  margin-left: 165px;
  margin-right: 50px;
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;
  Letter-spacing: 0%;
  position: absolute;
  
  
  
}

